const RT = (props) => {
  return (
    <table>
      <thead>
        <tr>
          <th> ID</th>
          <th> Name</th>
          <th> Email</th>
          <th> Password</th>
          <th> Role</th>
        </tr>
      </thead>
      <tbody>
        {props &&
          props.data.map((i, ind) => {
            return (
              <tr>
                <td>{i["id"]}</td>
                <td>{i["name"]}</td>
                <td>{i["email"]}</td>
                <td>{i["username"]}</td>
                <td>{i["id"] > 4 ? "Admin" : "Cus"}</td>
              </tr>
            );
          })}
      </tbody>
    </table>
  );
};
export default RT;
