import React, { useState, useEffect } from "react";
import "./ResponsiveForm.css";
import RT from "./ResponsiveTable";
import Module from "./Modules";
import { BrowserRouter, Routes, Route } from "react-router-dom";
const Forms = () => {
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [ner, sner] = useState("");
  const [eer, seer] = useState("");
  const [per, sper] = useState("");
  const [data, setData] = useState();
  useEffect(() => {
    (async () => {
      const url = "https://jsonplaceholder.typicode.com/users";
      const res = await fetch(url);
      const dt = await res.json();
      setData(dt);
    })();
  }, []);
  const validation = (e, type) => {
    e.preventDefault();
    const val = e.target.value;
    if (type === "name") {
      if (val.trim() === "") {
        sner("name required");
      } else {
        const na = data.filter((i) => i.name === val);
        console.log(na, "hi", val);
        if (na.length > 0) {
          setName(na[0].name);
        }
      }
    } else if (type === "email") {
      if (val.trim() === "" || val.indexOf("@") > -1) {
        seer("email required");
      } else {
        const em = data.filter((i) => i.email === val);
        if (em.length > 0) {
          setEmail(em[0].email);
        }
      }
    } else if (type === "password") {
      if (val.trim() === "" || val.match(/[a-zA-Z1-9@#$%^&*_]/g) === 0) {
        sper("password required");
      } else {
        const pd = data.filter((i) => i.username === val);
        if (pd.length > 0) {
          setPassword(pd[0].username);
        }
      }
    }
  };
  const submitForm = () => {
    // console.log("hiii", name, email, password);
  };
  return (
    <div>
      <div className="resForm" onSubmit={submitForm}>
        <div className="resFormGroup">
          <label>Name</label>
          {ner ? <span className="text-danger mt-2">{ner}</span> : ""}
          <input
            type="text"
            placeholder="Please enter name"
            onChange={(e) => validation(e, "name")}
          />
        </div>
        <div className="resFormGroup">
          <label>Email</label>
          {eer ? <span className="text-danger">{eer}</span> : ""}
          <input
            type="email"
            placeholder="Please enter email"
            onChange={(e) => validation(e, "email")}
          />
        </div>
        <div className="resFormGroup">
          <label>Password</label>
          {per ? <span className="text-danger">{per}</span> : ""}
          <input
            type="password"
            placeholder="Please enter password"
            onChange={(e) => validation(e, "password")}
          />
        </div>
        <button className="mt-2" onClick={submitForm}>
          Submit
        </button>
      </div>
      <BrowserRouter>
        <Routes>
          <Route exact path="/" />
          <Route path="/module" element={<Module />} />
          <Route path="/table" element={<RT data={data ? data : []} />} />
        </Routes>
      </BrowserRouter>
    </div>
  );
};

export default Forms;
